function display(){
  var square = $("section>div"),
      n = square.length,
      view = $("section").width(),
      w = square.width(),
      remain = n%(Math.floor(view/w)),
      s = n - remain;

  if(view > 689){
	for(var i = s; i < n; i++){
	  $("section>div:eq(" + i + ")").css("transform", "translateX(-310px)");
    }
  } else {
  	for(var i = s; i < n; i++){
	  $("section>div:eq(" + i + ")").css("transform", "translateX(0px)");
    }
  }
}

display();
$(window).resize(display);



